<!-- src/components/BannerSlider.vue -->
<template>
    <a-carousel autoplay>
      <div v-for="(banner, index) in banners" :key="index" class="banner-container">
        <img :src="banner" class="banner-img" />
      </div>
    </a-carousel>
  </template>
  
  <script>
  export default {
    name: 'BannerSlider',
    data() {
      return {
        banners: [
          require('@/assets/banner1.jpg'),
          require('@/assets/banner2.jpg'),
          require('@/assets/banner3.jpg')
        ]
      };
    }
  };
  </script>
  
  <style scoped>
  .banner-img {
    width: 100%;
    height: 500px;
    object-fit: cover;
  }
  .banner-container {
    text-align: center;
    overflow: hidden;
  }
  </style>
  