<template>
  <div class="catalog">
    <h1>–ö–∞—Ç–∞–ª–æ–≥ —Ç–æ–≤–∞—Ä–æ–≤</h1>
    <a-row gutter="24">
      <a-col :span="8" v-for="product in products" :key="product.id">
        <a-card :title="product.name" :hoverable="true" class="product-card">
          <img :src="product.image" alt="image" class="product-image" />
          <p>{{ product.description }}</p>
          <strong>{{ product.price }} ‚ÇΩ</strong>
          <div class="card-footer">
            <a-button type="primary" @click="addToCart(product)">–í –∫–æ—Ä–∑–∏–Ω—É</a-button>
          </div>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: 'CatalogPage',
  data() {
    return {
      products: [
        {
          id: 1,
          name: 'Samsung Galaxy S25+ (Mint)',
          price: 70600,
          image: 'https://via.placeholder.com/200x150?text=S25%2B+Mint',
          description: '–ù–æ–≤–∏–Ω–∫–∞ —Å Snapdragon 8 Elite.'
        },
        {
          id: 2,
          name: 'Samsung Galaxy S25+ (Silver)',
          price: 70600,
          image: 'https://via.placeholder.com/200x150?text=S25%2B+Silver',
          description: '–¢–æ–ø–æ–≤—ã–π —Å–º–∞—Ä—Ç—Ñ–æ–Ω –≤ –Ω–æ–≤–æ–º –¥–∏–∑–∞–π–Ω–µ.'
        },
        {
          id: 3,
          name: 'Samsung Galaxy S25 Ultra (Black)',
          price: 91600,
          image: 'https://via.placeholder.com/200x150?text=S25+Ultra+Black',
          description: '–§–ª–∞–≥–º–∞–Ω—Å–∫–∞—è –º–æ–¥–µ–ª—å —Å —É–ª—É—á—à–µ–Ω–Ω–æ–π –∫–∞–º–µ—Ä–æ–π.'
        }
      ]
    }
  },
  methods: {
    addToCart(product) {
      // –ü–æ–ª—É—á–∞–µ–º –∫–æ—Ä–∑–∏–Ω—É –∏–∑ localStorage –∏–ª–∏ —Å–æ–∑–¥–∞—ë–º –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤
      let cart = JSON.parse(localStorage.getItem('cart')) || []

      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –µ—Å—Ç—å –ª–∏ —Ç–æ–≤–∞—Ä –≤ –∫–æ—Ä–∑–∏–Ω–µ
      const exists = cart.some(item => item.id === product.id)

      if (!exists) {
        // –ï—Å–ª–∏ —Ç–æ–≤–∞—Ä–∞ –Ω–µ—Ç –≤ –∫–æ—Ä–∑–∏–Ω–µ, –¥–æ–±–∞–≤–ª—è–µ–º –µ–≥–æ
        cart.push(product)
        localStorage.setItem('cart', JSON.stringify(cart))  // –û–±–Ω–æ–≤–ª—è–µ–º localStorage
        this.$message.success(`–¢–æ–≤–∞—Ä –¥–æ–±–∞–≤–ª–µ–Ω –≤ –∫–æ—Ä–∑–∏–Ω—É: ${product.name}`)
      } else {
        // –ï—Å–ª–∏ —Ç–æ–≤–∞—Ä —É–∂–µ –µ—Å—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω–µ
        this.$message.warning('–¢–æ–≤–∞—Ä —É–∂–µ –≤ –∫–æ—Ä–∑–∏–Ω–µ')
      }
    }
  }
}
</script>

<style scoped>
.catalog {
  padding: 40px;
}

.product-card {
  margin-bottom: 24px;
}

.product-image {
  width: 100%;
  height: auto;
  margin-bottom: 16px;
}

.card-footer {
  margin-top: 12px;
  text-align: right;
}
</style>
